type Face = 'r' | 'g' | 'b' | 'y' | 'o' | 'w';
type Direction = 'clockwise' | 'counterclockwise';
type Move = { face: string; direction: Direction };

export class RubiksCube {
  private faces: { [key: string]: Face[] };
  private scrambleMoves: Move[] = [];

  constructor() {
    // Initialize a solved cube
    this.faces = {
      U: Array(9).fill('y'), // Up - Yellow
      D: Array(9).fill('w'), // Down - White
      F: Array(9).fill('r'), // Front - Red
      B: Array(9).fill('o'), // Back - Orange
      L: Array(9).fill('g'), // Left - Green
      R: Array(9).fill('b'), // Right - Blue
    };
  }

  private rotateArray(arr: Face[], direction: Direction): Face[] {
    const result = [...arr];
    if (direction === 'clockwise') {
      // For clockwise rotation: [0,1,2,3,4,5,6,7,8] -> [6,3,0,7,4,1,8,5,2]
      [result[0], result[1], result[2], result[3], result[4], result[5], result[6], result[7], result[8]] = 
      [result[6], result[3], result[0], result[7], result[4], result[1], result[8], result[5], result[2]];
    } else {
      // For counterclockwise rotation: [0,1,2,3,4,5,6,7,8] -> [2,5,8,1,4,7,0,3,6]
      [result[0], result[1], result[2], result[3], result[4], result[5], result[6], result[7], result[8]] = 
      [result[2], result[5], result[8], result[1], result[4], result[7], result[0], result[3], result[6]];
    }
    return result;
  }

  private updateAdjacentFaces(face: string, direction: Direction) {
    const adjacency: { [key: string]: { faces: string[]; indices: number[][] } } = {
      U: {
        faces: ['F', 'R', 'B', 'L'],
        indices: [[0, 1, 2], [0, 1, 2], [0, 1, 2], [0, 1, 2]],
      },
      D: {
        faces: ['F', 'L', 'B', 'R'],
        indices: [[6, 7, 8], [6, 7, 8], [6, 7, 8], [6, 7, 8]],
      },
      F: {
        faces: ['U', 'R', 'D', 'L'],
        indices: [[6, 7, 8], [0, 3, 6], [2, 1, 0], [8, 5, 2]],
      },
      B: {
        faces: ['U', 'L', 'D', 'R'],
        indices: [[0, 1, 2], [0, 3, 6], [8, 7, 6], [8, 5, 2]],
      },
      L: {
        faces: ['U', 'F', 'D', 'B'],
        indices: [[0, 3, 6], [0, 3, 6], [0, 3, 6], [8, 5, 2]],
      },
      R: {
        faces: ['U', 'B', 'D', 'F'],
        indices: [[8, 5, 2], [0, 3, 6], [8, 5, 2], [8, 5, 2]],
      },
    };

    const adj = adjacency[face];
    if (!adj) return;

    const { faces, indices } = adj;
    const values: Face[][] = faces.map((f, i) => indices[i].map(idx => this.faces[f][idx]));

    if (direction === 'clockwise') {
      values.unshift(values.pop()!);
    } else {
      values.push(values.shift()!);
    }

    faces.forEach((f, i) => {
      indices[i].forEach((idx, j) => {
        this.faces[f][idx] = values[i][j];
      });
    });
  }

  rotate(face: string, direction: Direction): void {
    if (!this.faces[face]) {
      throw new Error(`Invalid face: ${face}`);
    }

    this.faces[face] = this.rotateArray(this.faces[face], direction);
    this.updateAdjacentFaces(face, direction);
  }

  scramble(movesCount: number = 20): void {
    const faces = ['U', 'D', 'F', 'B', 'L', 'R'];
    const directions: Direction[] = ['clockwise', 'counterclockwise'];
    this.scrambleMoves = [];

    for (let i = 0; i < movesCount; i++) {
      const face = faces[Math.floor(Math.random() * faces.length)];
      const direction = directions[Math.floor(Math.random() * directions.length)];
      this.rotate(face, direction);
      this.scrambleMoves.push({ face, direction });
    }
  }

  getScrambleMoves(): Move[] {
    return [...this.scrambleMoves];
  }

  isSolved(): boolean {
    return Object.values(this.faces).every(face => 
      face.every(color => color === face[0])
    );
  }

  getStateString(): string {
    // Returns the cube state in the format expected by getCubeSvg()
    // Order: Up, Right, Front, Down, Left, Back
    return [
      ...this.faces['U'],
      ...this.faces['R'],
      ...this.faces['F'],
      ...this.faces['D'],
      ...this.faces['L'],
      ...this.faces['B'],
    ].join('');
  }
}
