'use client';

import { useState } from 'react';
import { RubiksCube } from '@/lib/RubiksCube';
import { RubiksCubeSolver } from '@/lib/RubiksCubeSolver';
import { getCubeSvg } from '@/lib/cubeUtils';

export default function TestPage() {
  const [cube, setCube] = useState(() => new RubiksCube());
  const [currentState, setCurrentState] = useState(cube.getStateString());
  const [solutionSteps, setSolutionSteps] = useState<Array<{ move: string; state: string }>>([]);
  const [message, setMessage] = useState('');

  const handleScramble = () => {
    console.log('Scramble clicked!');
    setMessage('Scrambling...');
    const newCube = new RubiksCube();
    newCube.scramble(10);
    setCube(newCube);
    setCurrentState(newCube.getStateString());
    setSolutionSteps([]);
    setMessage('Cube scrambled!');
  };

  const handleSolve = async () => {
    console.log('Solve clicked!');
    setMessage('Solving...');
    const solver = new RubiksCubeSolver();
    const solution = solver.solve(cube);
    
    if (solution.length === 0) {
      setMessage('Cube is already solved!');
      return;
    }

    setSolutionSteps(solution);
    
    // Animate through solution
    for (let i = 0; i < solution.length; i++) {
      setCurrentState(solution[i].state);
      setMessage(`Step ${i + 1}/${solution.length}: ${solution[i].move}`);
      await new Promise(resolve => setTimeout(resolve, 500));
    }
    
    setMessage('Cube solved!');
  };

  return (
    <div className="min-h-screen bg-gray-100 p-8">
      <div className="max-w-4xl mx-auto">
        <h1 className="text-4xl font-bold text-center mb-8">Rubik's Cube Test</h1>
        
        <div className="bg-white p-8 rounded-lg shadow-lg">
          <div className="flex justify-center mb-8">
            <div 
              dangerouslySetInnerHTML={{ __html: getCubeSvg(currentState) }}
              className="w-96 h-96"
            />
          </div>
          
          <div className="text-center mb-6">
            <p className="text-lg font-semibold">{message}</p>
          </div>
          
          <div className="flex gap-4 justify-center mb-8">
            <button
              onClick={handleScramble}
              className="bg-blue-500 hover:bg-blue-600 text-white px-8 py-4 text-xl font-bold rounded-lg"
            >
              Scramble
            </button>
            <button
              onClick={handleSolve}
              className="bg-green-500 hover:bg-green-600 text-white px-8 py-4 text-xl font-bold rounded-lg"
            >
              Solve
            </button>
          </div>
          
          <div className="max-h-64 overflow-y-auto">
            <h3 className="text-lg font-semibold mb-2">Solution Steps:</h3>
            {solutionSteps.map((step, index) => (
              <div key={index} className="p-2 bg-gray-100 rounded mb-1">
                {index + 1}. {step.move}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
