export function getCubeSvg(state: string): string {
  // Validate input
  if (!state || state.length !== 54) {
    throw new Error('Invalid cube state: must be a string of length 54');
  }

  const colorMap: { [key: string]: string } = {
    'r': '#FF0000', // red
    'g': '#00FF00', // green
    'b': '#0000FF', // blue
    'y': '#FFFF00', // yellow
    'o': '#FFA500', // orange
    'w': '#FFFFFF', // white
  };

  // Create SVG for a single face
  const createFace = (startIndex: number, x: number, y: number, size: number) => {
    const squares = [];
    for (let i = 0; i < 3; i++) {
      for (let j = 0; j < 3; j++) {
        const color = colorMap[state[startIndex + i * 3 + j]] || '#CCCCCC';
        squares.push(`
          <rect
            x="${x + j * (size/3)}"
            y="${y + i * (size/3)}"
            width="${size/3}"
            height="${size/3}"
            fill="${color}"
            stroke="black"
            stroke-width="1"
          />
        `);
      }
    }
    return squares.join('');
  };

  // Layout configuration for the faces
  // Order: Up(0), Right(9), Front(18), Down(27), Left(36), Back(45)
  const faceLayout = [
    { startIndex: 0, x: 100, y: 0 },    // Up
    { startIndex: 9, x: 200, y: 100 },   // Right
    { startIndex: 18, x: 100, y: 100 },  // Front
    { startIndex: 27, x: 100, y: 200 },  // Down
    { startIndex: 36, x: 0, y: 100 },    // Left
    { startIndex: 45, x: 300, y: 100 },  // Back
  ];

  const size = 100; // Size of each face
  const faces = faceLayout.map(face => 
    createFace(face.startIndex, face.x, face.y, size)
  ).join('');

  return `
    <svg 
      viewBox="-10 -10 420 320" 
      width="100%" 
      height="100%" 
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect x="-10" y="-10" width="420" height="320" fill="#f8f8f8" />
      ${faces}
    </svg>
  `;
}
